<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b></b></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/store/storeAdmins' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>
    <el-col :span="24" class="warp-main"
            v-loading="loading"
            element-loading-text=""
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.8)">
      <!--  -->
      <el-col :span="24" class="toolbar">
        <el-form :inline="true" class="demo-form-inline" >
            <el-input :placeholder="placeholder" v-model="keywords" style="width: 30%;">
              <el-select class="sel-placeholder" v-model="select" @change="searchFieldChange" slot="prepend" style="width:130px">
                <el-option label="" value="name"></el-option>
                <el-option label="" value="channelType"></el-option>
                <el-option label="" value="bailPercentage"></el-option>
              </el-select>
              <el-button slot="append" icon="el-icon-search" @click="getStores"></el-button>
            </el-input>
            <template v-if="userInfo.role === 'ADMIN'">
              <el-button type="info" plain icon="el-icon-plus" @click="dialogCreateStoreVisible = true"></el-button>
            </template>
        </el-form>
      </el-col>

      <!-- -->
      <el-table :data="stores"
                style="width: 100%"
                height="680"
                ref="table"
                @selection-change="tableSelectionChange">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="right" inline class="demo-table-expand">
              <el-form-item label="storeId">
                <span>{{ props.row.code }}</span>
              </el-form-item>
              <el-form-item label="appKey">
                <span>{{ props.row.app.key }}</span>
              </el-form-item>
              <el-form-item label="appSecret">
                <span>{{ props.row.app.secret }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column prop="channels[0].paymentGateway" label="" align="center"></el-table-column>
        <el-table-column prop="channelType" label="" align="center"></el-table-column>
        <el-table-column prop="name" label="" align="center"></el-table-column>
        <el-table-column prop="bailPercentage" sortable label="" align="center"></el-table-column>
        <el-table-column prop="quota" sortable label="" align="center"></el-table-column>
        <el-table-column prop="todayTradeAmount" sortable label="" align="center"></el-table-column>
        <el-table-column prop="dailyLimit" sortable label="" align="center"></el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button @click="view(scope.row)" type="text" size="small"></el-button>
          </template>
        </el-table-column>
        <template v-if="this.userInfo.role === 'ADMIN'">
          <el-table-column label="" align="center">
            <template slot-scope="scope">
              <el-button class="handle"
                         size="mini" type="info" plain
                         @click="setCurrent(scope.row)">
              </el-button>
              <el-button class="handle"
                         size="mini" type="primary" plain
                         @click="increaseQuota(scope.row)">
              </el-button>
              <el-button class="handle"
                         size="mini" type="danger" plain
                         @click="dialogCreateChannelVisible = true">ID
              </el-button>
            </template>
          </el-table-column>
        </template>
      </el-table>

      <el-dialog title="" center v-model="dialogCreateStoreVisible" :visible.sync="dialogCreateStoreVisible" @close="resetCreateStore">
        <el-form id="#createStore" :model="createStore" :rules="createStoreRules" ref="createStore" label-width="120px">
          <el-form-item label="" prop="name">
            <el-input v-model="createStore.name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="bailPercentage">
            <el-input v-model="createStore.bailPercentage">
              <template slot="append">%</template>
            </el-input>
          </el-form-item>
          <el-form-item label="" prop="channelType">
            <el-select v-model="createStore.channelType" placeholder="">
              <el-option label="" value="WECHAT"></el-option>
              <el-option label="" value="ALIPAY"></el-option>
              <el-option label="H5" value="H5"></el-option>
              <el-option label="APP" value="APP"></el-option>
              <el-option label="" value="BANK"></el-option>
            </el-select>
          </el-form-item>

        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button plain @click="dialogCreateStoreVisible = false"> </el-button>
          <el-button type="primary" plain :loading="createLoading" @click="handleCreateStore"></el-button>
        </div>
      </el-dialog>

      <!-- -->
      <el-dialog title="" center v-model="dialogUpdateVisible" :visible.sync="dialogUpdateVisible" @close="resetUpdate">
        <el-form id="#update" :model="update" :rules="updateRules" ref="update" label-width="120px">
          <el-form-item label="" prop="name">
            <el-input v-model="update.name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="bailPercentage">
            <el-input v-model="update.bailPercentage">
              <template slot="append">%</template>
            </el-input>
          </el-form-item>
          <el-form-item label="" prop="dailyLimit">
            <el-input v-model="update.dailyLimit">
              <template slot="append"></template>
            </el-input>
          </el-form-item>
          <el-form-item label="" prop="csrTel">
            <el-input v-model="update.csrTel"></el-input>
          </el-form-item>
          <el-form-item label="" prop="proxyUrl">
            <el-input v-model="update.proxyUrl"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogUpdateVisible = false"> </el-button>
          <el-button type="primary" :loading="updateLoading" @click="updateStore"> </el-button>
        </div>
      </el-dialog>

      <!-- -->
      <el-dialog title="" center v-model="dialogIncreaseVisible" :visible.sync="dialogIncreaseVisible" width="30%">
        <el-form ref="formIncrease" :model="formIncrease" label-width="80px">
          <el-form-item label="">
            <el-input-number v-model="formIncrease.amount" :min="100" :step="100"></el-input-number>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogIncreaseVisible = false"> </el-button>
          <el-button type="primary" @click="handleIncreaseQuota"></el-button>
        </div>
      </el-dialog>

      <!-- ID-->
      <el-dialog title="ID" center v-model="dialogCreateChannelVisible" :visible.sync="dialogCreateChannelVisible" @close="resetCreateChannel">
        <el-form id="#createChannel" :model="createChannel" :rules="createChannelRules" ref="createChannel" label-width="120px">
          <el-form-item label="ID" prop="extStoreId">
            <el-input v-model="createChannel.extStoreId"></el-input>
          </el-form-item>
          <el-form-item label="" prop="extStoreName">
            <el-input v-model="createChannel.extStoreName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="paymentGateway">
            <el-select v-model="createChannel.paymentGateway" placeholder="">
              <el-option label="H5" value="CHINAUMSH5"></el-option>
              <el-option label="APP" value="CHINAUMSAPP"></el-option>
              <el-option label="" value="IPS"></el-option>
            </el-select>
          </el-form-item>
          <template v-if="createChannel.paymentGateway === 'CHINAUMSH5'||createChannel.paymentGateway === 'CHINAUMSAPP'">
            <el-form-item label="" prop="tid">
              <el-input v-model="createChannel.chinaUmsProps.tid"></el-input>
            </el-form-item>
            <el-form-item label="ID" prop="msgSrcId">
              <el-input v-model="createChannel.chinaUmsProps.msgSrcId"></el-input>
            </el-form-item>
            <el-form-item label="" prop="msgSrc">
              <el-input v-model="createChannel.chinaUmsProps.msgSrc"></el-input>
            </el-form-item>
            <el-form-item label="" prop="signKey">
              <el-input v-model="createChannel.chinaUmsProps.signKey"></el-input>
            </el-form-item>
            <el-form-item label="" prop="InstMid">
              <el-input v-model="createChannel.InstMid"></el-input>
            </el-form-item>
          </template>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogCreateChannelVisible = false"> </el-button>
          <el-button type="primary" @click="handleCreateChannel"></el-button>
        </div>
      </el-dialog>

      <el-pagination class="paging"
                     :current-page="filter.currentPage"
                     :page-sizes="[10, 20, 50, 100]"
                     :page-size="filter.pageSize"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="totalRows"
                     @size-change="pageSizeChange"
                     @current-change="pageCurrentChange">
      </el-pagination>
    </el-col>
  </el-row>
</template>

<script>
  let placeholders = {
    "name":"",
    "bailPercentage":"",
    "channelType":"",
  };

  export default {
    data: function() {
      return {
        userInfo:{
          id:'',
          account:'',
          name:'',
          role:''
        },
        agentsInfo:{},
        stores: [],
        update:{
          name: '',
          appId: '',
          bailPercentage: '',
          code: '',
          csrTel: ':,:',
          proxyUrl: '',
          dailyLimit: ''
        },
        createStore: {
          name: '',
          bailPercentage: '',
          appId: '',
          agentId: '',
          channelType: ''
        },
        createChannel: {
          extStoreId: "",
          extStoreName: "",
          paymentGateway: "",
          chinaUmsProps: {
            tid: "",
            msgSrcId: "",
            msgSrc: "",
            signKey: "",
            InstMid: ""
          },
        },
        formEdit: {
          account: '',
          password: '',
          name: '',
          role: ''
        },
        formIncrease: {
          amount: '',
          transaction_type: 'FREE'
        },
        storeId: '',

        createAdminRules: {
          account: [
            { required: true, message: '', trigger: 'blur' },
          ],
          password: [
            { required: true, message: '', trigger: 'blur' },
          ],
          name: [
            { required: true, message: '', trigger: 'blur' },
          ],
          role: [
            { required: true, message: '', trigger: 'blur' },
          ]
        },
        createStoreRules: {
          name: [
            { required: true, message: '', trigger: 'blur' },
          ],
          bailPercentage: [
            { required: true, message: '', trigger: 'blur' },
          ],
          channelType: [
            { required: true, message: '', trigger: 'blur' },
          ]
        },
        createChannelRules: {
          extStoreId: [
            { required: true, message: 'ID', trigger: 'blur' },
          ],
          extStoreName: [
            { required: true, message: '', trigger: 'blur' },
          ],
          paymentGateway: [
            { required: true, message: '', trigger: 'blur' },
          ]
        },
        updateRules: {
          name: [
            { required: true, message: '', trigger: 'blur' },
          ],
          csrTel: [
            { required: true, message: '', trigger: 'blur' },
          ],
          proxyUrl: [
            { required: true, message: '', trigger: 'blur' },
          ]
        },
        filter: {
          pageSize: 10,
          currentPage: 1,
          beginIndex: 0,
          name: '',
          bailPercentage: '',
          code: '',
          csrTel: '',
          proxyUrl: ''
        },
        totalRows: 0,
        keywords: '', //
        select: 'name', //
        loading: true,
        selected: [], //
        dialogCreateStoreVisible: false,
        dialogCreateChannelVisible: false,
        dialogIncreaseVisible: false,
        dialogUpdateVisible: false,
        createLoading: false,
        updateLoading: false,
        placeholder:placeholders["name"]
      };
    },
    mounted: function() {
      let userInfo = sessionStorage.getItem('access-user');
      if (userInfo) {
        userInfo = JSON.parse(userInfo);
        this.userInfo.id = userInfo.id;
        this.userInfo.account = userInfo.account;
        this.userInfo.name = userInfo.name;
        this.userInfo.role = userInfo.role;
      }

      this.getStores()
    },
    methods: {
      tableSelectionChange(val) {
        this.selected = val;
      },
      searchFieldChange(val) {
        this.select = val;
        this.placeholder=placeholders[val];
        console.log(` ${val} `);
      },
      pageSizeChange(val) {
        console.log(` ${val} `);
        this.filter.pageSize = val;
        this.getStores();
      },
      pageCurrentChange(val) {
        console.log(`: ${val}`);
        this.filter.currentPage = val;
        this.getStores();
      },
      // 
      resetCreateStore() {
        this.$refs.createStore.resetFields();
      },
      resetUpdate() {
        this.$refs.update.resetFields();
      },
      resetCreateChannel() {
        this.$refs.createChannel.resetFields();
      },
      //
      getStores() {
        this.loading = true;
        let adminId = sessionStorage.getItem('currentAdminId');
        this.$http.get(`http://www.wfpay.xyz/xpay/admin/${adminId}/stores/`).then(res => {
            if(res.data.data.constructor !== Array){
              this.stores.push(res.data.data);
              console.log(this.stores)
            }
            else this.stores = res.data.data;
            this.stores.forEach(val =>{
              if(val.channelType === 'WECHAT'){
                val.channelType = ''
              }
              if(val.channelType === 'ALIPAY'){
                val.channelType = ''
              }
              if(val.channelType === 'BANK'){
                val.channelType = ''
              }
              val.bailPercentage += '%';
              val.quota += '';
              val.todayTradeAmount += '';
              val.dailyLimit += '';
            });
          //
          let queryData = [];
          if(this.keywords !==""){
            for (var i=0,lenI=this.stores.length;i<lenI;i++) {
              let reg = new RegExp(this.keywords);
              if(this.stores[i][this.select].toString().match(reg)){
                queryData.push(this.stores[i]);
              }
            }
          }
          else queryData = this.stores;
          this.totalRows = queryData.length;
          //
          this.filter.beginIndex = (this.filter.currentPage-1)*this.filter.pageSize;
          this.stores = queryData.splice(this.filter.beginIndex,this.filter.pageSize);
          this.loading = false;
          this.selected.splice(0,this.selected.length);
        })
      },
      view(row){
        this.$refs.table.toggleRowExpansion(row);
      },
      handleCreateStore(){
        this.$refs.createStore.validate((valid) => {
          if (valid) {
            this.createLoading=true;
            this.createStore.appId = sessionStorage.getItem('currentAppId');
            this.createStore.agentId = sessionStorage.getItem('currentAgentId');
            this.createStore.adminId = sessionStorage.getItem('currentAdminId');
            this.$http.put(`http://www.wfpay.xyz/xpay/admin/${this.userInfo.id}/stores`,this.createStore).then(() => {
              this.$message.success('');
              this.resetCreateStore();
              this.dialogCreateStoreVisible = false;
            }).catch(() => {
              this.$message.error('');
              this.resetCreateStore();
            })
          }
          else {
            return false;
          }
        })
      },
      setCurrent(row){
        this.update.id=row.id;
        this.update.name=row.name;
        if(row.csrTel){
          this.update.csrTel=row.csrTel;
        }
        this.update.proxyUrl=row.proxyUrl;
        this.update.bailPercentage = parseInt(row.bailPercentage);
        this.update.dailyLimit = parseInt(row.bailPercentage);
        this.dialogUpdateVisible=true;
      },
      //
      updateStore(){
        this.$refs.update.validate((valid) => {
          if (valid) {
            this.updateLoading=true;
            this.$http.patch(`http://www.wfpay.xyz/xpay/admin/${this.userInfo.id}/stores/${this.update.id}`,this.update).then(res => {
              this.$message.success('');
              this.dialogUpdateVisible = false;
              this.updateLoading = false;
              this.resetUpdate();
              this.getStores();
            }).catch(() =>{
              this.$message.error('');
              this.resetUpdate();
              this.dialogUpdateVisible = false;
              this.updateLoading = false;
            })
          }
          else {
            return false;
          }
        });
      },
      increaseQuota(row) {
        this.storeId = row.id;
        this.dialogIncreaseVisible = true;
      },
      handleIncreaseQuota(){
        let id = this.userInfo.id;
        let storeId = this.storeId;
        this.$http.post(`http://www.wfpay.xyz/xpay/admin/${id}/stores/${storeId}/quota`,this.formIncrease).then(() => {
          this.$message.success('');
          this.dialogIncreaseVisible = false;
          this.getStores();
        }).catch(() => {
          this.$message.error('');
        })
      },
      handleCreateChannel(){
        this.$refs.createChannel.validate((valid) => {
          if (valid) {
            this.createLoading = true;
            this.$http.put(`http://www.wfpay.xyz/xpay/admin/${this.userInfo.id}/channels`,this.create).then(res => {
              console.log(res);
              this.$message.success('');
              this.dialogCreateVisible = false;
              this.createLoading = false;
              resetCreateChannel();
              this.getChannels();
            })
          }
          else {
            return false;
          }
        });
      },
      //
      viewChannel(row){
        if(row.channels){
          sessionStorage.setItem('channelData', JSON.stringify(row.channels));
          this.$router.push({ path: '/store/storeChannels' });
        }
        else {
          this.$message.error('')
        }
     }
    }
  }
</script>

<style>
  .paging{
    text-align: center;
    margin:12px 0;
  }
  .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 5%;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 100%;
  }
  .el-dialog__body{
    margin: 0 20%;
  }
  .handle{
    margin: 8px auto;
  }
</style>



